import React from 'react'
import All_other_project from './All_other_project'
import All_other_img from './All_other_img'

const Other_project = () => {
    return (
        <>
            <div className='container-fluid' style={{backgroundColor:'#0A192F',paddingTop:'70px',paddingBottom:'200px'}}>
            <div className='row'>
                <div className='col-md-5 col-xs-0'>
                </div>
                <div className='col-md-2 col-xs-12'>
                    <font className='other_me'>Other Project</font>
                </div>
                <div className='col-md-5 col-xs-0'></div>

               
            </div>


            <div className='row'>
                <div className='col-md-1 col-xs-0'></div>
                
                {
                        All_other_img.map((val,ind) =>{
                            return <All_other_project key={ind} imgsrc={val.imgsrc} title={val.title} contant={val.contant} />
                        })
                }
                
                <div className='col-md-1 col-xs-0'></div> 
            </div>
            </div>  
        </>
    )
}

export default Other_project
