import React from 'react'
import All_pro from './All_pro'
import All_pro_img from './All_pro_img'

const Mul_pro = () => {
    return (
        <>
            <div className='container-fluid' style={{backgroundColor:'#0A192F',paddingTop:'130px',paddingBottom:'200px'}}>
            <div className='row' id='projects'>
                <div className='col-md-2 col-xs-0'>
                </div>
                <div className='col-md-6 col-xs-12'>
                    <font className='ex_me'>Some Things I've Built</font>
                </div>
                
                <div className='col-md-2 col-xs-0'></div>
            </div>

            <div className='row'>
                {
                        All_pro_img.map((val,ind) =>{
                            return <All_pro key={ind} imgsrc={val.imgsrc} title={val.title} contant={val.contant} />
                        })
                }
            
            </div>
           
          
                
        </div>  
        </>
    )
}

export default Mul_pro
