import React, {useState} from 'react'
import { NavLink } from 'react-router-dom'
import Mul_exp from './Mul_exp'
import All_exp_data from './All_exp_data'

const Experience = () => {

    const [active,setActive] = useState('FirstCard')

    return (
        <>
            <div className='container-fluid' style={{backgroundColor:'#0A192F',paddingTop:'200px',paddingBottom:'130px'}}>
                <div className='row' id="experience">
                    <div className='col-md-3 col-xs-0 ex_me'></div>
                    <div className='col-md-3 col-xs-12 ex_me'>
                        My Experience
                    </div>
                    <div className='col-md-3 col-xs-12 ex_me'></div>
                    <div className='col-md-3 col-xs-0'> </div>
                </div>

                <div className='row'>
                    <div className='col-md-3 col-xs-0 ex_me'></div>
                    <div className='col-md-2 col-xs-12' style={{paddingTop:'40px'}}>
                        <ul className="navbar-nav ml-auto mb-2 mb-lg-0 exp_nev_link">
                            <span></span>
                            <li className="nav-item">
                                <button className='exp_btn' onClick={() => setActive("FirstCard")} ><span>Internship</span></button>
                            </li>
                            <li className="nav-item">
                                <button className='exp_btn' onClick={() => setActive("SecondCard")} ><span></span></button>
                            </li>
                           
                            <li className="nav-item">
                                 <button className='exp_btn' onClick={() => setActive("ThirdCard")}><span></span></button>
                            </li>
                            
                        </ul>
                
                    </div>
                    {active === 'FirstCard' && <Mul_exp data={All_exp_data} cardIndex={0}/>}
                    {active === 'SecondCard' && <Mul_exp data={All_exp_data} cardIndex={1}/>}
                    {active === 'ThirdCard' && <Mul_exp data={All_exp_data} cardIndex={2}/>}
                    
                    <div className='col-md-3 col-xs-0'> </div>
                </div>
            </div>
        </>
    )
}

export default Experience
