import React from 'react'
import { NavLink } from 'react-router-dom'
import logo from '../src/img/logo.jpg'
import {Link} from 'react-scroll'
import pdf from './img/bhakti_patel.pdf'


const Menu = () => {
    
    return (
        <>
        
            <nav className="navbar navbar-expand-lg navbar-light" style={{backgroundColor:'#0A192F'}}>
            <div className="container-fluid">
                <NavLink to='/'>  <img src={logo} style={{width:"60px"}} alt="logo"/></NavLink>
                    <button className="navbar-toggler"  type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
                    <span className="navbar-toggler-icon"></span>
                    </button>
                <div className="collapse navbar-collapse" id="navbarSupportedContent">
                <ul className="navbar-nav ml-auto mb-2 mb-lg-0">
                        <li className="nav-item">
                        <Link activeClassName="menu_active" exact className="nav-link" style={{color:'white'}} to="/"></Link>
                        </li>
                        <li className="nav-item">
                        <Link activeClassName="menu_active" spy={true} smooth={true} offset={-70} duration={500} exact className="nav-link" style={{color:'white',cursor:'pointer'}} to="about">About</Link>
                        </li>
                        <li className="nav-item">
                        <Link activeClassName="menu_active" spy={true} smooth={true} offset={-70} duration={500} exact className="nav-link" style={{color:'white',cursor:'pointer'}} to="experience">Experience</Link>
                        </li>
                        <li className="nav-item">
                        <Link activeClassName="menu_active" spy={true} smooth={true} offset={-70} duration={500} exact className="nav-link" style={{color:'white',cursor:'pointer'}} to="projects">Projects</Link>
                        </li>
                        <li className="nav-item">
                        <Link activeClassName="menu_active" spy={true} smooth={true} offset={-70} duration={500} exact className="nav-link" style={{color:'white',cursor:'pointer'}} to="contact">Contact</Link>
                        </li>
                        
                </ul>
                <button type="button" class="rbtn"><a  style={{color:'white',textDecoration:'none'}} href={pdf}>Resume</a></button>
                </div>
            </div>
            </nav> 
        
  </>
    )
}

export default Menu
