import React from 'react'

const Mul_exp = ({data,cardIndex}) => {
    return (
        <>
        {data[cardIndex].map(item =>(
            <div className='col-md-4 col-xs-12 ex_me' style={{paddingTop:'25px'}}>
            <font className='exp_data'>{item.heding}<span className='exp_data2'>{item.heding1}</span></font>
            <h6 style={{color:'rgb(148, 161, 178)',fontSize:'14px',letterSpacing:'1px'}}>{item.title}</h6>
            <p className='exp_pra'><font className="exp_icon">{item.icon1}</font>{item.contant1} </p>
            <p className='exp_pra'><font className="exp_icon">{item.icon2}</font>{item.contant2}</p>
            <p className='exp_pra'><font className="exp_icon">{item.icon3}</font>{item.contant3}</p>
        
            </div> 
        ))}
           
        </>
    )
}

export default Mul_exp
