import React from 'react'


const All_pro = (props) => {
    return (
        <>
             
                    <div className='col-md-2 col-xs-0'></div>
                            <div className='col-md-4 col-xs-12'>
                                <img src={props.imgsrc} alt="props.imgsrc" className='pro_img' />
                            </div>
                            <div className='col-md-4 col-xs-12'>
                                <h2 className='pro_comm'>{props.title}</h2>
                                <p className='pro_pra'>{props.contant}</p>
                            </div>
                     <div className='col-md-2 col-xs-0'></div>


                    

                    
            
        </>
    )
}

export default All_pro
