import React from 'react'
import facebook from '../src/img/facebook.png'
import ins from '../src/img/ins.png'
import link from '../src/img/link.png'


const Contact = () => {
    return (
        <>
            <div className='container-fluid' style={{backgroundColor:'rgb(10, 25, 47)',paddingBottom:'280px'}}>
                <div className='row' id='contact'>
                        <div className='col-md-2 col-xs-0'></div>
                        <div className='col-md-8 col-xs-12' style={{textAlign:'center'}}>
                            <font className='cont_next'>What's Next?</font>
                            <h1 className='cont_get'>Get In Touch</h1>
                            <p className='cont_i'>I enjoy talking to new people so feel free to connect with me <br/> 🤖</p>
                            <br/>
                            <a href='https://www.facebook.com/profile.php?id=100009960279559'>  <img src={facebook} style={{width:"40px"}} alt="logo"/></a>
                            <a href='https://www.instagram.com/patel._.bhakti/'>  <img src={ins} style={{width:"40px"}} alt="logo"/></a>
                            <a href='http://linkedin.com/in/bhakti-patel-28026517b'>  <img src={link} style={{width:"40px"}} alt="logo"/></a>
                
                            
                            </div>
                        <div className='col-md-2 col-xs-0'></div>
                </div>
            </div>
        </>
    )
}

export default Contact
