import React from 'react'

const All_other_project = (props) => {
    return (
        <>
                       
                       
                       <div className="col-md-3  mt-5 mx-auto card_body">
                            <div className="card" style={{backgroundColor:'#0A192F'}}>
                                    <img src={props.imgsrc} className="card-img-top img" alt="props.imgsrc" />
                                    <div className="card-body">
                                      <font className="card-heding">
                                          {props.title}
                                        </font>
                                      <p className="card-text">{props.contant}</p>
                                    </div>
                                  </div>
                        </div>

                     
                       
        </>
    )
}

export default All_other_project
