import React from 'react'
import me from '../src/img/me.jpg'


const About = () => {
    return (
        <>
             
            <div className="container-fluid" >
                <div className="row" style={{backgroundColor:'#0A192F',paddingTop:'700px'}}>
                    <div className="col-md-2 col-xs-0">
                        
                    </div>
                    <div className="col-md-4 col-xs-6">
                        <font className='ab_me' id='about'>About Me</font>
                        <p className='ab_pra'>
                            Hello! I'm Bhakti Patel, pursuing MCA from Nirma University.<br/>
                            When it comes to technology, there was always something that attracted me to learn and experiment with new things. Exploring new things and creating something new is all that I do.<br/><br/>
                            The goal is to make something with which I can say that I have contributed something to the world! As a growing child, my parents always nurtured me and never let me stay in the comfort zone. This motivated me to work hard and so I will make every possible effort to repay their struggle.<br/><br/>
                            I am very adaptive to any new Technology as it is my passion to explore new technologies. When it comes to my work, I put all of my dedication towards it. <br/><br/>
                            Here are a few technologies I've know throughly :<br/>
                        </p>
                                    <div className='row ab_part'>
                                        <div className='col-md-6 col-xs-6'>
                                            Wordpress💙<br/>
                                            Javascript<br/>
                                            React.js<br/>
                                            DBMS<br/>
                                            C++<br/>
                                           
                                        </div>
                                        <div className='col-md-6 col-xs-6'>
                                            HTML5<br/>
                                            CSS<br/>
                                            PHP 🔥<br/>
                                            C<br/>
                                            
                                        </div>
                                    </div>
                        <p className='ab_pra'>Some of my Hobbies :</p>
                                    <div className='row ab_part'>
                                        <div className='col-md-6 col-xs-6'>
                                            Listening Songs <br />
                                            Traveling
                                        </div>
                                        <div className='col-md-6 col-xs-6'>
                                            Sketching
                                        </div>
                                    </div>
                    </div>
                    <div className="col-md-4 col-xs-6 img_line">
                            <span></span>
                            <span></span>
                            <span></span>
                            <span></span>
                            <img src={me} alt="" className="img_me" style={{width:"60",height:'30'}} />
                    </div>
                    <div className="col-md-2 col-xs-0">
                         
                    </div>
                </div>
            </div>
        </>
    )
}

export default About
